import React, { useState } from 'react';
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import CssBaseline from '@mui/material/CssBaseline';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
    appBar: {
        position: 'absolute',
        bottom:0,
        marginBottom: '0px',
        zIndex: 0,
        width: "100%",
        "&.MuiAppBar-root":{
            backgroundColor: '#242A32',
        }
    },
    info:{
        fontSize: '12px',
        fontWeight: 600,
        color: '#7C7F84',
        margin:"3px 0"
    },
    info1:{
        fontSize: '12px',
        fontWeight: 700,
        color: "#FFFFFF",
        margin:"3px 0"
    },
}));

const MenuFooter = () => {
    const cls = useStyles();
    
    return (
        <>
        <AppBar position="static" className={cls.appBar} >
          <Container>
            <Box display="flex" justifyContent="space-around"style={{margin:"55px 50px 20px"}}>
                <Box>
                    <img src="/footerlogo.png" />
                    <Box className={cls.info}>     <br/>     <br/>  .</Box>
                </Box>
                <Box>
                  <Box style={{ fontSize: '13px', fontWeight: "bold", color:"#FFFFFF" }}></Box>
                  <Box className={cls.info}></Box>
                  <Box className={cls.info}></Box>
                  <Box className={cls.info}></Box>
                  <Box className={cls.info}></Box>
                </Box>  
                <Box>
                  <Box style={{ fontSize: '13px', fontWeight: "bold", color:"#FFFFFF" }}></Box>
                  <Box className={cls.info}> </Box>
                  <Box className={cls.info}>1:1 </Box>
                  <Box className={cls.info}></Box>
                  <Box className={cls.info}> </Box>
                  <Box className={cls.info}></Box>
                  <Box className={cls.info}>API </Box>
                  <Box className={cls.info}> </Box>
                  <Box className={cls.info1}> </Box>
                  <Box className={cls.info1}> </Box>
                </Box>   
                <Box>
                  <Box style={{ fontSize: '13px', fontWeight: "bold", color:"#FFFFFF" }}></Box>
                  <Box className={cls.info}> : <b style={{color:"#FFFFFF"}}>1661-5566 (365  08:00~23:00 /  : 23:00~08:00)</b></Box>
                  <Box className={cls.info}>  : <b style={{color:"#FFFFFF"}}>listing@bithumbcorp.com</b></Box>
                  <Box className={cls.info}>  : <b style={{color:"#FFFFFF"}}>partnership@bithumbcorp.com</b></Box>
                  <Box className={cls.info}>  : <b style={{color:"#FFFFFF"}}>whistle@bithumbcorp.com</b></Box>
                  <Box className={cls.info}> :      489 1 1~2 ( :  737-10)<br/>
                                                         :   10 00 ~  6 00 (,  )</Box>
                  <Box className={cls.info}>  : @( )</Box>
                </Box>   
            </Box>
          </Container>
        </AppBar>            
        </>
    );
}

export default MenuFooter;